.navbar {
  background-color: white;
  height: 19px;
  display: flex;
  align-items: center;
  top: 0;
  z-index: 1;
  width: 100%;
  border-bottom: 1px solid #2196f7;
  padding: 10px 0px;
}
a {
  text-decoration: none;
  color: black;
}
li {
  padding: 0 15px;
}
.navbar-element {
  display: flex;
  align-items: center;
  list-style-type: none;
  padding: 0;
}

.dropdown-bloc {
  display: none;
  position: absolute;
  background-color: #f9f9f9;
  width: 150px;
  height: min-content;
  border: 1px solid #2196f7;
  left: -3px;
  top: 30px;
}
.dropdown-bloc li {
  padding: 6px 0px;

  display: block;
  font-size: 12px;
}
.dropdown-bloc li:hover {
  background-color: #f1f1f1;
}
.menu {
  position: relative;
}
.menu li:hover .dropdown-bloc {
  display: block;
}

.dropdown-bloc a:hover {
  color: #2196f7;
}
a:hover {
  color: #2196f7;
}
